body{
  background-image: radial-gradient(at center center, rgb(0, 0, 0), rgb(23, 24, 26), #2c2c30);
  background-repeat: no-repeat;
  height: 100vh;
}

.nav {
  background-color: #2c2c30;
  color: rgb(255, 255, 255);
  display: flex;
  width: 100%;
  top: 0rem;
  justify-content: flex-end;
  height: 4rem;
  text-align: center;
  border: solid 1px white;
  font: bold;
  font-size: 1.2rem;
  font-family: Georgia, "Times New Roman", Times, serif;
  position: absolute;
  left: 50%;
  transform: translate(-50%);
}
#aboutme{
  margin-right: 1rem;
}

.links {
  display: flex;
  padding: 0.1rem;
  width: 20%;
  justify-content: space-between;
  position: fixed;
}
#aboutme:hover,
#portfolio:hover,
#contact:hover,
#skills:hover {
  font-size: x-large;
  cursor: pointer;
  transition: font-size 0.6s ease-in-out;
}
.imgContainer{
  display: flex;
  width: 50rem;
  height: 40rem;
  position: absolute;
  left: 60%;
  top: 20%;
}
.img{
  width: 30rem;
  height: 40rem;
  position: absolute;
  left: 60%;
  top: 20%;
  border-radius: 10px;
}
.aboutCard{
  display: flex;
  border-radius: 10px;
  border: solid 8px #2323ac;
  top: 60%;
  left: 40%;
}

a {
  transition: transform 0.3s ease-in-out;
}

a:hover, a:active {
  transform: scale(1.1);
}